html {
  box-sizing: border-box;
  font-size: 18px;
  background-color: #fff;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

body {
  margin: 0 auto;
  padding: 0.5rem 2rem;
  line-height: 1.4;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  color: #222;
}

section {
  margin-bottom: 2rem;
}

.indent {
  margin-left: 1rem;
}

h1,
h2,
h3,
h4 {
  line-height: 1.0;
  font-family: "Verdana", "Geneva", sans-serif;
}

h1 {
  font-size: 2.5rem;
}

h2 {
  font-size: 2rem;
}

h3 {
  font-size: 1.75rem;
}

h4 {
  font-size: 1.5rem;
}

h5 {
  font-size: 1rem;
  font-weight: 300;
}

@media (max-width: 800px) {
  h1 {
    font-size: 2rem;
  }
  h2 {
    font-size: 1.75rem;
  }
  h3 {
    font-size: 1.5rem;
  }
  h4 {
    font-size: 1.25rem;
  }
}

b,
strong,
h1,
h4 {
  font-weight: 700;
}

em,
i {
  font-style: italic;
}

small {
  font-size: 0.75em;
}

big {
  font-size: 1.25em;
}

ul li {
  list-style-type: square;
}

ol li {
  list-style-type: decimal;
}
